@tailwind base;
@tailwind components;
@tailwind utilities;
body {
  --button-border-radius: 50%;
  --button-bg: transparent;
  --button-stroke: #ce1352;
  --button-stroke-width: 1px;
  --button-text: #ffffff;
  --button-bg-hover: transparent;
  --button-stroke-hover: #ce1352;
  --button-stroke-width-hover: 1px;
  --button-text-hover: #ffffff;
  --cursor-stroke: #fff;
  --cursor-fill: #fff;
  --cursor-stroke-width: 1px;
  --color-text: #ffffff;
  --color-bg: #312b35;
  --color-link: #89639e;
  --color-link-hover: #ce1352;
  --button-filler: #ce1352;
}

.font-outline {
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #F2F2F2;
}

.cursor {
  display: none;
}

@media (any-pointer: fine) {
  .cursor {
    @apply fixed top-0 left-0 block pointer-events-none;
  }
  .cursor__inner {
    fill: var(--cursor-fill);
    stroke: var(--cursor-stroke);
    stroke-width: var(--cursor-stroke-width);
  }
}
.btn-cta {
  @apply font-monumentExtend font-bold text-red text-[42px] uppercase py-4 border-[2px] border-red rounded-[240px] transition-all duration-300 ease-in-out flex justify-center items-center w-[716px] overflow-hidden bg-transparent;
  -moz-appearance: none;
  -webkit-appearance: none;
}
.btn-cta:focus, .btn-cta--hover {
  @apply outline-none text-dark;
}
.btn-cta__deco {
  @apply absolute top-0 left-0 w-full h-full overflow-hidden;
}
.btn-cta__deco-filler {
  @apply absolute w-[150%] h-[200%] rounded-[50%] -top-1/2 -left-1/4 bg-red;
  transform: translate3d(0, 75%, 0);
}
.btn-cta__text, .btn-cta__text-inner {
  @apply flex items-center justify-center w-full h-full;
}